<template>
	<div id="finance-index">
		<public-header title="我的账户"></public-header>
		<div class="content">
			<div class="account bg-primary">
				<div class="font-14 margin-15-b">账户可用余额(元)</div>
				<div>¥{{deliveryer.credit2}}</div>
			</div>
			<van-cell-group class="margin-10-t">
				<van-cell value="" is-link :to="util.getUrl({path: 'pages/finance/current'})">
					<div slot="title" class="flex">
						<i class="icon icon-account"></i>
						<span>账户明细</span>
					</div>
				</van-cell>
				<van-cell value="" is-link :to="util.getUrl({path: 'pages/finance/getcashList'})">
					<div slot="title" class="flex">
						<i class="icon icon-text1"></i>
						<span>提现记录</span>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell value="" is-link :to="util.getUrl({path: 'pages/finance/getcash'})">
					<div slot="title" class="flex">
						<i class="icon icon-refund"></i>
						<span>申请提现</span>
					</div>
				</van-cell>
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				deliveryer: {},
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'delivery/finance/getcash/index',
					autoAssign: true,
					variable: 'deliveryer'
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#finance-index .account{
		text-align: center;
		font-size: 30px;
		padding: 40px 0;
	}
	#finance-index .icon{
		font-size: 18px;
		padding-right: 5px;
		color: #0EC3B3;
	}
</style>


